.otherSkills {
  padding: 2rem;

  h2 {
    text-align: center;
    font-size: 2rem;
    font-weight: 700;
    margin-bottom: 2rem;
    color: $principal_color;
  }

  .skillsList {
    display: flex;
    flex-wrap: wrap;
    gap: 1.5rem;
    justify-content: center;
    list-style: none;

    .left,
    .right {
      flex: 1 1 280px;
      display: flex;
      flex-direction: column;
      background-color: #ffffff;
      border-radius: 12px;
      padding: 1rem;
      box-shadow: 0 6px 15px rgba(0, 0, 0, 0.08);
      transition: transform 0.3s ease, box-shadow 0.3s ease;

      &:hover {
        transform: translateY(-6px);
        box-shadow: 0 10px 20px rgba(0, 0, 0, 0.15);
      }

      ul {
        padding: 0;
        margin: 0;
        list-style: none;

        li {
          display: flex;
          align-items: center;
          padding: 0.5rem;
          margin-bottom: 0.8rem;
          font-size: 1.1rem;
          color: #444;
          border-radius: 8px;
          transition: all 0.3s ease;
          box-shadow: 0 2px 5px rgba(0, 0, 0, 0.05);

          &:hover {
            background-color: $principal_color;
            color: white;
            font-size: 1.25rem;
            box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
          }

          i {
            margin-right: 10px;
            font-size: 1.2rem;
            color: $second_color;
            background-color: $principal_color;
            width: 40px;
            height: 40px;
            display: flex;
            justify-content: center;
            align-items: center;
            text-align: center;
            border-radius: 50%;
            transition: transform 0.3s ease, background-color 0.3s ease;

            &:hover {
              transform: scale(1.2);
              background-color: $second_color;
              color: $principal_color;
            }
          }
        }
      }
    }
  }

  /* Responsive */
  @media screen and (max-width: 850px) {
    .skillsList {
      flex-direction: column;
      gap: 1rem;

      .left,
      .right {
        width: 100%;
      }
    }
  }
}
